<template src="./switchTab.html"></template>
<style src="./switchTab.less" lang="less"></style>
<script>
import { Bus } from '../bus/bus';
import { actionLog } from '../../../src/api/actionLogSDK.js';
export default {
  name: 'switch-tab',
  props: ['tabs'],
  data () {
    return {
      iscur: 0
    }
  },
  created () {
  },
  methods: {
    tabChange (index) {
      if (index !== this.iscur) {
        this.iscur = index;
        this.$emit('on-tab-switch', index);
        // Bus.$emit('onTabSwitch', index)
      }
    }
  }

}
</script>



