<template>
  <!-- <div class=""> -->
    <div class="fiy-slide"
         @click="clickHandler">
      <slot/>
    </div>
  <!-- </div> -->
</template>
<style scoped></style>
<script>
export default {
  name: 'slider-item',
  methods: {
    clickHandler () {
      this.$emit('click');
    }
  }
}
</script>



